<template>
  <div class="hello">
    <h1>FileServer ThreadPool watch</h1>
    <el-row>
      <el-button @click="openWs">open</el-button>
      <el-button @click="startCache">startCache</el-button>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div id="chart" class="chart-box"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { cacheConver } from "../ecm-api";
import * as echarts from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { LineChart } from "echarts/charts";
import {
  TitleComponent,
  // TooltipComponent,
  GridComponent,
} from "echarts/components";

export default {
  name: "SysMonitor",
  data() {
    return {
      msg: "test",
      watchData: [],
      ws: null,
      threadPoolWatchChart: null,
      threadPoolWatchChartSeriesData: [],
    };
  },
  mounted() {
    this.initChart();
  },
  methods: {
    openWs() {
      const uid = this.$store.state.userInfo.id;
      this.ws = new WebSocket(
        "ws://localhost:9322/file-server/open-ws-conn/web_" + uid
      );
      const _this = this;
      this.ws.onopen = function () {
        console.log("onopen");
        _this.ws.send(
          JSON.stringify({ wsMessageType: "THREAD_POOL_WATCH", data: "open" })
        );
      };
      this.ws.onmessage = function (e) {
        let data = JSON.parse(e.data).data;
        if (_this.threadPoolWatchChartSeriesData.length > 59) {
          _this.threadPoolWatchChartSeriesData.shift();
        }
        _this.threadPoolWatchChartSeriesData.push({
          name: data.time,
          value: ["2021/12/24T" + data.time, data.active],
        });
        _this.updateChart();
      };
      this.ws.onclose = function () {
        console.log("onclose");
      };
      this.ws.onerror = function (error) {
        console.log("onerror");
        console.log(error);
      };
    },
    initChart() {
      echarts.use([TitleComponent, GridComponent, LineChart, CanvasRenderer]);
      this.threadPoolWatchChart = echarts.init(
        document.getElementById("chart")
      );
      this.threadPoolWatchChart.setOption({
        title: {
          left: "center",
          bottom: 5,
          text: "",
          textStyle: {
            fontSize: 12,
          },
        },
        xAxis: {
          type: "time",
          axisLabel: {
            formatter: function (val) {
              return new Date(val).toTimeString().slice(3, 8);
            },
          },
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            name: "",
            type: "line",
            data: this.threadPoolWatchChartSeriesData,
          },
        ],
      });
    },
    updateChart() {
      this.threadPoolWatchChart.setOption({
        series: [
          {
            data: this.threadPoolWatchChartSeriesData,
          },
        ],
      });
    },
    startCache() {
      cacheConver();
    },
  },
  beforeUnmount() {
    if (this.ws) {
      this.ws.close();
    }
  },
};
</script>

<style scoped>
.chart-box {
  width: 100%;
  height: 300px;
}
</style>
